.AboutText{

text-align: center;
font-size: 20px;
margin-top: 3%;
margin-left: 30%;
margin-right: 30%;
animation: transitionIn 1000ms;

}

.picture{

    position: relative;
    width: 15%;
    height: auto;
    animation: transitionIn 1000ms;
    box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
    border-radius: 10%;

}

.selectAll{

    margin-top: -3%;
}




@keyframes transitionIn {
    from {
        opacity: 0;
        transform: translateY(-30px);
    }

    to {    

        opacity: 1;
        transform: translateY(0);
    }


}